<script>
	export default {
		data() {
			return {
				item1: {},
				item2: {},
				item3: {},
				item4: {},
				item5: {},
				item6: {},
				show1: 0,
				show2: 0,
				show3: 0,
				show4: 0,
				show5: 0,
				show6: 0,
				current: 0,
				hidepic: null,
				showpic: null
			}
		},
		methods: {
			gotohist() {
				uni.navigateTo({
					url: "../historymessage/historymessage"
				})
			},
			gettitle1() {
				uni.request({
					method: 'GET',
					url: "https://jsonplaceholder.typicode.com/posts",
					success: res => {
						this.item1 = {
							title: res.data[Math.floor(Math.random() * 90 + 1)].title,
							picsrc: "../../static/test.jpg"
						}
						// this.show1 = Math.floor(Math.random() * 7 + 1)
						this.show1 = 1
						setTimeout(e => {this.show1 = 0;this.ushow1=!this.ushow1}, 15000)
