<style lang="scss">
	.hist {
		width: 750rpx;
		height: 140rpx;
		background-color: #dadada;
		position: fixed;
		top: var(--window-top);
		display: flex;
		justify-content: center;
		align-items: center;

		.history {
			width: 300rpx;
			position: absolute;
			right: 30rpx;
		}
	}

	.tubebox {
		width: 750rpx;
		height: 680rpx;
		margin-top: 300rpx;
		display: flex;
		flex-direction: column;

		.tubebox1 {
			flex: 1;
			display: flex;
			justify-content: center;

			.tube {
				width: 140rpx;
				height: 140rpx;
				position: relative;
				border: 1rpx solid black;
				background-color: #dadada;
				border-radius: 50%;
			}

		}

		.tubebox2 {
			flex: 1;
			display: flex;
			justify-content: center;

			.tube {
				width: 140rpx;
				height: 140rpx;
				min-width: 140rpx;
				position: relative;
				border: 1rpx solid black;
				border-radius: 50%;
			}
		}
	}

	.tail {
		width: 750rpx;
		background-color: #dadada;
		position: fixed;
		bottom: var(--window-bottom);
		display: flex;
		justify-content: center;

		.custom {
			width: 150rpx;
			height: 150rpx;
			padding: 20rpx 0;

			image {
				width: 100%;
				height: 100%;
				border-radius: 50%;
			}
		}
	}

	.say {
		width: 200rpx;
		line-height: 1.5em;
		position: absolute;
		right: 50rpx;
		bottom: 64rpx;
		text-align: center;
		border-radius: 15rpx;
	}
	.fade-leave,
	.fade-enter-to{
		opacity: 1;
	}
	.fade-leave-active,
	.fade-enter-active{
		transition: all 1.5s;
	}
	.fade-leave-to,
	.fade-enter{
		opacity: 0;
	}
</style>
